.home_wrapper {
  height: 80vh;
  width: 100vm;


  @media (max-width: 900px) {
    height: 95vh;
  }

  @media (max-width: 389px) {
    min-height: 100vh;
    transform: translateY(5%);
  }
  @media (min-width: 2000px) {
    height: 85vh;
    transform: translateY(25%);
  }
}

.skills_wrapper {
  @media (max-width: 400px) {
    min-height: 80vh;
  }

  .skills_text {
    text-align: center;
    margin-bottom: 10vh;
    margin-top: 20vh;

    @media (max-width: 450px) {
      margin-top: 10vh;
    }
  }

  .skills-item {
    text-align: center;
    height: 120px;
    width: 90px;
    cursor: pointer;

    @media (max-width: 400px) {
    margin: -5px;
    }
    @media (min-width: 2000px) {
      margin-left: 6px;
      .h6 {
        font-size: 150%;
      }
    }

    img {
      width: 80px;
      align-items: center;
      align-content: center;

      @media (max-width: 450px) {
        width: 40px;
        margin: 0;
        padding: 0;
      }

      @media (min-width: 2000px) {
        width: 100px;
      }
    }
  }
}

.portfolio_wrapper {
  min-height: 100vh;



  @media (min-width: 2000px) {
    height: 100vh;
  }


  .portfolio_text {
    text-align: center;
    padding-top: 20vh;
    margin-bottom: 5vh;
  }

  .portfolio-item {
    text-align: center;
    border-radius: 0.5rem;
    margin: 0.5rem;
    padding: 0.5rem;
    width: 270px;
    cursor: pointer;
    transition: all 0.3s ease;
    height: 335px;
    box-shadow: 0px 5px 10px 2px rgba(60, 90, 80, 0.3);
    background-color: rgba(0, 0, 0, 0.14);

    @media (max-width: 900px) {
      height: 335px;
      width: 245px;
      margin: 10px;
    }

    @media (max-width: 600px) {
      width: 270px;
      margin-top: 50px;
      height: 335px;
    }

    @media (min-width: 2000px) {
      width: 500px;
      margin-top: 50px;
      height: 440px;
    }

    .portfolio-img {
      width: 100%;
      height: 180px;
      position: relative;
      img {
        height: 150px;
        margin-top: 15px;
        width: 100%;

        @media (min-width: 2000px) {
          height: 250px;
          margin-top: 15px;
          width: 100%;
        }
      }
      .portfolio-hover {
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.5);
        border-radius: 0.5rem;
        opacity: 0;
        transition: all 0.3s ease;
        @media (min-width: 2000px) {
          height: 155%;
        }
      }
    }
    .portfolio-content {
      @media (min-width: 2000px) {
        margin-top: 105px;
      }
      h3 {
        font-family: 'DM Sans', sans-serif;
        color: #202124;
        @media (min-width: 2000px) {
          font-size: 150%;
        }
      }
      p {
        font-family: 'DM Sans', sans-serif;
        color: rgba(32, 33, 36, 0.78);
        @media (min-width: 2000px) {
          font-size: 120%;
        }
      }
    }
    .content-button {
      top: -2%;
      font-size: 15px;
      background-color: rgba(32, 33, 36, 0.24);
      color: #202124;
    }
  }
  @media (max-width: 900px) {
    .portfolio-item:nth-last-child(-n + 2) {
      margin-bottom: 5rem;
    }
  }
  @media (max-width: 500px) {
    .portfolio-item:nth-last-child(-n + 2) {
      margin-bottom: 0rem;
    }
    .portfolio-item:last-child {
      margin-bottom: 5rem;
    }
  }
}

.footer_block {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;

  .socials_icon {
    font-size: 4vh;
  }

  @media (min-width: 2000px) {
    height:10vh;
  }
}

.header_text {
  @media (min-width: 2000px) {
    font-size: 40px;
  }
}

.portfolio_wrapper,
.footer_block {
  background-image: linear-gradient(
    -40deg,
    #9f77ff,
    #f0567b,
    #ff984d,
    #fddf62,
    #8ff8a2,
    #49beed,
    #9f77ff
  );
  background-size: 1000% 1000%;
  animation: gradient 7s ease infinite alternate;
  @keyframes gradient {
    0% {
      background-position: 0% 50%;
    }
    100% {
      background-position: 100% 50%;
    }
  }
}
